//个人信息界面
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { getCurrentProfile, deleteAccout } from '../../actions/profileActions';
import PropTypes from 'prop-types';
import Spinner from '../../common/Spinner';
import { Link } from 'react-router-dom';
import ProfileActives from './ProfileActives';
import Experience from './Experience';
import Education from './Education';
import Eproject from './Eproject';


class Dash extends Component {
    //即将渲染到页面之前的生命周期
    componentDidMount() {
        document.title = '个人页面';
        //调用action
        //请求数据，获取个人用户信息
        this.props.getCurrentProfile();
    }
    onDeleteClick(e) {
        //console.log('test')
        //调用action
        this.props.deleteAccout();
    }
    render() {
        const { user } = this.props.auth;
        const { profile, loading } = this.props.profile;
        let dashboardContent;

        //判断profile是否为空 或者 loading是否为真
        if (profile === null || loading) {
            dashboardContent = <Spinner />
        } else {
            //检查对象中是否有数据
            if (Object.keys(profile).length > 0) {
                //有数据
                dashboardContent = (
                    <div>
                        <p className="lead text-muted">
                            Welcome <Link to={`/profile/${profile.handle}`}>{user.name}</Link>
                        </p>

                        <ProfileActives />
                        {/* 个人经历 & 教育经历 & 项目经历*/}
                        <Experience experience={profile.experience} />
                        <Education education={profile.education} />
                        <Eproject eproject={profile.eproject} />
                        {/* 删除当前账户 */}
                        <button
                            data-toggle="modal"
                            data-target="#exampleModalCenterdash"
                            // onClick={e => this.onDeleteClick(e)}
                            style={{ marginBottom: '60px' }}
                            className="btn btn-danger">
                            删除当前账户
                        </button>
                        {/* <!-- Modal --> */}
                        <div className="modal fade" id="exampleModalCenterdash" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                            <div className="modal-dialog modal-dialog-centered" role="document">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalCenterTitle">* 提示 *</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        是否删除当前账户！！！</div>
                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-dismiss="modal">取消</button>
                                        <button type="button"
                                            onClick={e => this.onDeleteClick(e)}
                                            data-dismiss="modal"
                                            className="btn btn-primary">确定</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            } else {
                //用户已经登录，但是未添加任何个人信息数据
                dashboardContent = (
                    <div>
                        <p className="lead text-muted">欢迎 {user.name}</p>
                        <p>没有任何相关的个人信息, 请添加一些您的个人信息</p>
                        <Link className="btn btn-lg btn-info" to="/create-profile">创建个人信息</Link>
                    </div>
                )
            }
        }
        return (
            <div className="dashboard">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <h1 className="display-4">Dash</h1>
                            {dashboardContent}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

Dash.propTypes = {
    getCurrentProfile: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    auth: state.auth,
    profile: state.profile
})

export default connect(mapStateToProps, { getCurrentProfile, deleteAccout })(Dash);