//删除个人履历
export const deleteExperience = id => dispatch => {
    axios.delete("/api/profile/experience")
        .then(res =>
            dispatch({
                type: GET_PROFILE,
                payload: res.data
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        )
}

