//删除当前账户
export const deleteAccout = () => dispatch => {
    axios.delete("/api/profile")
        .then(res =>
            dispatch({
                type: SET_CURRENT_USER,
                payload: {}
            }))
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            }))
}

//获取所有人员信息
export const getProfiles = () => dispatch => {
    //加载动画
    dispatch(setProfileLoading())
    axios.get("/api/profile/all")
        .then(res =>
            dispatch({
                type: GET_PROFILES,
                payload: res.data
            }))
        .catch(err =>
            dispatch({
                type: GET_PROFILES,
                payload: null
            }))

}

//获取个人信息
export const getProfileByHandle = (handle) => dispatch => {
        //加载动画
        dispatch(setProfileLoading())
        axios.get(`/api/profile/handle/${handle}`)
            .then(res =>
                dispatch({
                    type: GET_PROFILE,
                    payload: res.data
                }))
            .catch(err =>
                dispatch({
                    type: GET_PROFILE,
                    payload: null
                }))
    }
    //加载动画
export const setProfileLoading = () => {
        return {
            type: PROFILE_LOADING
        }
    }
    //点击退出时清除profile
export const clearCurrentProfile = () => {
        return {
            type: CLEAR_CURRENT_PROFILE
        }
    }
    //在页面渲染之前清空errors,让第一次页面不显示报错信息
export const clearProfileErrors = () => {
    return {
        type: GET_ERRORS,
        payload: null
    }
}