import { GET_ERRORS, SET_CURRENT_USER, SET_STATUS_USER } from './types';
import axios from 'axios';
import setAuthToken from '../utils/setAuthToken';
import jwt_decode from 'jwt-decode';

//注册
export const registerUser = (userData, history) => dispatch => {
    //axios请求
    // 在页面上显示详细错误信息：err.response.data
    axios.post("/api/users/register", userData)
        .then(res => {
            const { status } = res
            dispatch(setStatusUser(status))
                // this.props.message.success('欢迎')
            history.push("/login")
        })
        .catch(err => dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        }));
}

//登录
export const loginUser = userData => dispatch => {
    axios.post("/api/users/login", userData)
        .then(res => {
            // console.log(res)
            const { token } = res.data;
            // console.log(res.data)
            //存储token到LS
            localStorage.setItem("jwtToken", token);
            //设置axios的headers token(工具)
            setAuthToken(token);

            //解析token
            const decoded = jwt_decode(token);
            // console.log(decoded);
            dispatch(setCurrentUser(decoded));
        })
        .catch(err => dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        }));
}

//当前状态
export const setCurrentUser = decoded => {
    return {
        type: SET_CURRENT_USER,
        payload: decoded
    }
}

export const setStatusUser = status => {
    return {
        type: SET_STATUS_USER,
        payload: status
    }
}

//退出功能
export const quitUser = () => dispatch => {
    //删除ls中token
    localStorage.removeItem("jwtToken");
    //干掉请求头
    setAuthToken(false);
    //连接reducer
    dispatch(setCurrentUser({}));
}