import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import TextFieldGroup from '../../common/TextFieldGroup';
import TextAreaFieldGroup from '../../common/TextAreaFieldGroup';
import { addEducation } from '../../actions/profileActions';
import '../../assets/golab.scss'



class AddEducation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            school: '',
            degree: '',
            fieldofstudy: '',
            from: '',
            to: '',
            description: '',
            errors: {},
            current: false,
            disabled: false
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            })
        }
    }
    onExpChange(e) {
        this.setState(
            { [e.target.name]: e.target.value }
        )
    }
    onCheck(e) {
        this.setState({
            disabled: !this.state.disabled,
            current: !this.state.current
        })
    }
    onSubmit(e) {
        e.preventDefault();
        const eduData = {
            school: this.state.school,
            degree: this.state.degree,
            fieldofstudy: this.state.fieldofstudy,
            from: this.state.from,
            to: this.state.to,
            current: this.state.current,
            description: this.state.description
        }
        this.props.addEducation(eduData,this.props.history)
    }

    render() {
        const { errors } = this.state;
        return (
            <div className="add-Education mgt">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <Link to="/dash" className="btn btn-light">
                                返回
                            </Link>
                            <h1 className="display-4 text-center">添加教育经历</h1>
                            <p className="lead text-center">
                                你所毕业的学校, 参加的培训等
                            </p>
                            <small className="d-block pb-3">* 为必填项</small>
                            {/* 表单 */}
                            <form onSubmit={e => this.onSubmit(e)}>
                                <TextFieldGroup
                                    placeholder="* 学校"
                                    name="school"
                                    value={this.state.school}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.school}
                                />
                                <TextFieldGroup
                                    placeholder="* 学历"
                                    name="degree"
                                    value={this.state.degree}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.degree}
                                />
                                <TextFieldGroup
                                    placeholder="* 专业"
                                    name="fieldofstudy"
                                    value={this.state.fieldofstudy}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.fieldofstudy}
                                />
                                <h6>* 开始时间</h6>
                                <TextFieldGroup
                                    name="from"
                                    type="date"
                                    value={this.state.from}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.from}
                                />
                                <h6>毕业时间</h6>
                                <TextFieldGroup
                                    name="to"
                                    type="date"
                                    value={this.state.to}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.to}
                                    disabled={this.state.disabled ? 'disabled' : ''}
                                />
                                <div className="form-check mb-4">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        name="current"
                                        value={this.state.current}
                                        checked={this.state.current}
                                        onChange={() => this.onCheck()}
                                        id="current"
                                    />
                                    <label htmlFor="current" className="form-check-label">
                                        当前在校
                                </label>
                                </div>
                                <TextAreaFieldGroup
                                    placeholder="在校表现"
                                    name="description"
                                    value={this.state.description}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.description}
                                    info="说说你在学习过程的经历"
                                />
                                <input
                                    type="submit"
                                    value="提交"
                                    className="btn btn-info btn-block mt-4"
                                />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
AddEducation.propTypes = {
    addEducation: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    profile: state.profile,
    errors: state.errors
});

export default connect(mapStateToProps, {addEducation})(withRouter(AddEducation));