import React, { Component } from 'react';

class ProfileApps extends Component {
    render() {
        const { eproject } = this.props;
        const eprItems = eproject.map(epr => (
            <div className="jumbotron">
                <h2>{epr.projectname}</h2>
                <h3>{epr.technology}</h3>
                <h3>{epr.tools}</h3>
                <p className="lead">{epr.description}</p>
                <div className="d-flex justify-content-between align-items-center">
                <div></div>
                <p>{epr.from} ～ {epr.to === '' ? '至今' : epr.to}</p>
                </div>
            </div>
        ))
        return (
            <div className="mt-3">
                <h3 className="text-center text-info ">项目经历</h3>
                <div>
                    {eprItems.length > 0 ? (
                        <ul className="d-flex justify-content-between  list-group">{eprItems}</ul>) : (
                            <p className="text-center">没有任何项目经历</p>
                        )}
                </div>
            </div>
        );
    }
}

export default ProfileApps;