import React, { Component } from 'react'
// import axios from 'axios';
// import classnames from 'classnames';
/** react-redux 的两个最主要功能
 *  connect: 用于从 UI 组件生成容器组件, 将两种组件连起来
 *  Provider: 可以让组件及子组件拿到state
 */
import { connect } from 'react-redux';
import { registerUser } from '../../actions/authActions';
/**
 * 属性类型检查
 */
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import TextFieldGroup from '../../common/TextFieldGroup';

class Register extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            password: '',
            password2: '',
            errors: {}
        };
    }

    onChange(e) {
        this.setState(
            { [e.target.name]: e.target.value }
        );
    }
    onSubmit(e) {
        e.preventDefault();
        const newUser = {
            name: this.state.name,
            email: this.state.email,
            password: this.state.password,
            password2: this.state.password2
        };
        //调用action
        this.props.registerUser(newUser, this.props.history)
        //axios请求
        // 在页面上显示详细错误信息：err.response.data
        // axios.post("/api/users/register", newUser)
        //     .then(res => console.log(res.data))
        //     .catch(err => this.setState({ errors: err.response.data }));
    }
    //增加路由守卫
    componentDidMount() {
        if (this.props.auth.isAuthenticated) {
            this.props.history.push("/dash");
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            })
        }
    }

    render() {
        //获取后台返回的errors
        //使用结构的写法
        const { errors } = this.state;
        // const errors = this.state.errors;
        // const user = this.props.auth.user;
        return (
            <div className="register">
                {/* {user ? user.name : null} */}
                <div className="contanier">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="text-center">注册</h1>
                            <p className="lead text-center">创建新的账户</p>
                            <form onSubmit={e => this.onSubmit(e)}>
                                <TextFieldGroup
                                    type="text"
                                    placeholder="Username"
                                    name="name"
                                    value={this.state.name}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.name}
                                />
                                {/* <div className="form-group">
                                    <input value={this.state.name} onChange={this.onChange} type="text"
                                        className={classnames('form-control form-control-lg', {
                                            'is-invalid': errors.name
                                        })} placeholder="用户名" name="name" />
                                    {
                                        errors.name && (<div className="invalid-feedback">
                                            {errors.name}
                                        </div>)
                                    }
                                </div> */}
                                <TextFieldGroup
                                    type="email"
                                    placeholder="Email"
                                    name="email"
                                    value={this.state.email}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.email}
                                    info="**我们使用gravatar全球公认头像,如果需要有头像显示，请使用在gravatar注册的邮箱**"
                                />
                                {/* <div className="form-group">
                                    <input value={this.state.email} onChange={this.onChange} type="email"
                                        className={classnames('form-control form-control-lg', {
                                            'is-invalid': errors.email
                                        })} placeholder="邮箱地址" name="email" />
                                    {
                                        errors.email && (<div className="invalid-feedback">
                                            {errors.email}
                                        </div>)
                                    }
                                    <small className="form-text text-muted">我们使用gravatar全球公认头像,如果需要有头像显示，请使用在gravatar注册的邮箱</small>
                                </div> */}
                                <TextFieldGroup
                                    type="password"
                                    placeholder="Password"
                                    name="password"
                                    value={this.state.password}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.password}
                                />
                                {/* <div className="form-group">
                                    <input value={this.state.password} onChange={this.onChange} type="password"
                                        className={classnames('form-control form-control-lg', {
                                            'is-invalid': errors.password
                                        })} placeholder="密码" name="password" />
                                    {
                                        errors.password && (<div className="invalid-feedback">
                                            {errors.password}
                                        </div>)
                                    }
                                </div> */}
                                <TextFieldGroup
                                    type="password"
                                    placeholder="Confirm password"
                                    name="password2"
                                    value={this.state.password2}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.password2}
                                />
                                {/* <div className="form-group">
                                    <input value={this.state.password2} onChange={this.onChange} type="password"
                                        className={classnames('form-control form-control-lg', {
                                            'is-invalid': errors.password2
                                        })} placeholder="确认密码" name="password2" />
                                    {
                                        errors.password2 && (<div className="invalid-feedback">
                                            {errors.password2}
                                        </div>)
                                    }
                                </div> */}
                                <input type="submit" className="btn btn-info btn-block mt-4" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Register.propTypes = {
    registerUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    status: PropTypes.object.isRequired
     
}
//将状态转化(映射)为属性
const mapStateToProps = (state) => ({
    auth: state.auth,
    errors: state.errors,
    status: state.errors
})
// export default Register;
export default connect(mapStateToProps, { registerUser })(withRouter(Register));
