//快捷键 rcc: 生成一个与状态的组件 react class compo nent
import React, { Component } from 'react'
import { Link,withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import '../../assets/golab.scss';

/**
 * 属性类型检查
 */
import PropTypes from 'prop-types';
import { quitUser } from '../../actions/authActions';
import { clearCurrentProfile } from '../../actions/profileActions';

class Navbar extends Component {
    //解决适配至移动端样式(折叠面版)时，点击导航切换路由，面版不隐藏的问题
    componentWillReceiveProps(nextProps){
        // var comment = document.getElementById('profiles');
        // const {pathname} = this.props.location;
        // if(nextProps.location.pathname !== pathname){
        //     comment.className="default"
        // }
        var nav = document.getElementById('mobile-nav');       
        const {pathname} = this.props.location;
        if(nextProps.location.pathname !== pathname || nextProps.location.pathname === pathname){
            nav.className="collapse navbar-collapse";
        }
    }
    //点击退出
    quitClick(e) {
        e.preventDefault();
        // this.props.clearCurrentProfile();
        this.props.quitUser();
    }
    render() {
        const { isAuthenticated, user } = this.props.auth;
        //遍历
        //登录状态
        const authLinks = (
            <ul className="navbar-nav ml-auto">
                <li className="nav-item active">
                    <Link className="nav-link" to="/feed">
                        评论
                    </Link>
                </li>
                <li className="nav-item active" style={{ marginRight: '20px' }}>
                    <Link className="nav-link" to="/dash">dash</Link>
                </li>
                <li className="nav-item active">
                    <img className="rounded-circle" style={{ display: 'inline-block', width: '35px', marginRight: '2px', verticalAlign: 'middle' }}
                        src={user.avatar} alt={user.name} />
                    <Link style={{ display: 'inline-block' }} className="nav-link"
                        onClick={(e) => this.quitClick(e)} to="">
                        退出
                    </Link>
                </li>
            </ul>
        );

        //未登录状态
        const noLinks = (
            <ul className="navbar-nav ml-auto">
                <li className="nav-item active">
                    <Link className="nav-link"
                        to="/register">注册</Link></li>
                <li className="nav-item active">
                    <Link className="nav-link"
                        to="/login">登录</Link></li>
            </ul>
        );

        return (
            <div>
                <nav id="activeNav" className="navbar navbar-expand-sm navbar-dark bg-dark mb-4 fixed-top">
                    <div className="container">
                        <Link to="/" className="navbar-brand">Awang</Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse"
                            data-target="#mobile-nav">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        {/* 导航栏 */}
                        <div className="collapse navbar-collapse" id="mobile-nav" data-toggle="collapse" >
                            <ul className="navbar-nav mr-auto">
                                <li className="nav-item active" id="profiles">
                                    <Link to="/profiles" className="nav-link">开发者</Link>
                                </li>
                            </ul>
                            {isAuthenticated ? authLinks : noLinks}
                            {/* <ul className="navbar-nav ml-auto">
                                <li className="nav-item">
                                    <Link className="nav-link"
                                        to="/register">注册</Link></li>
                                <li className="nav-item">
                                    <Link className="nav-link"
                                        to="/login">登录</Link></li>
                            </ul> */}
                        </div>
                    </div>
                </nav>
            </div>
        )
    }
}

Navbar.propTypes = {
    quitUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
}
//将状态转化(映射)为属性
const mapStateToProps = (state) => ({
    auth: state.auth,
})

//withRouter高阶组件作用:手动切换路由，通过传入history属性进行路由切换
export default connect(mapStateToProps, { quitUser, clearCurrentProfile })(withRouter(Navbar));
