import React, { Component } from 'react'
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import { deleteExperience } from '../../actions/profileActions';
class Experience extends Component {
    onDeleteClick(id) {
        this.props.deleteExperience(id);
    }
    render() {
        const experience = this.props.experience.map(exp => (
            <tr key={exp._id}>
                <td>{exp.company}</td>
                <td>{exp.title}</td>
                <td>
                    {exp.from} ~ {exp.to === '' ? '至今' : exp.to}
                </td>
                <td>
                    <button
                        data-toggle="modal"
                        data-target="#exampleModalCenterexp"
                        // onClick={this.onDeleteClick.bind(this, exp._id)}
                        className="btn btn-danger">
                        删除</button>
                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="exampleModalCenterexp" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalCenterTitle">提示</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    是否删除 {exp.company} 履历</div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">取消</button>
                                    <button type="button"
                                    data-dismiss="modal"
                                        onClick={this.onDeleteClick.bind(this, exp._id)}
                                        className="btn btn-primary">确定</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </td>
            </tr>
        ))
        return (
            <div>
                <h4 className="mb-4">个人履历</h4>
                <table className="table">
                    <thead>
                        <tr>
                            <th>公司</th>
                            <th>职位</th>
                            <th>年份</th>
                            <th />
                        </tr>
                        {experience}
                    </thead>
                </table>
            </div>
        )
    }
}
Experience.propTypes = {
    deleteExperience: PropTypes.func.isRequired
}
export default connect(null, { deleteExperience })(Experience);