import React, { Component } from 'react'
import classnames from 'classnames';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
// import axios from 'axios';
import { deletePost, addLike, removeLike } from '../../actions/postActions';
import moment from 'moment';

class PostItem extends Component {
    onDeleteCLick(id) {
        this.props.deletePost(id);
    }
    onLikeCLick(id) {
        this.props.addLike(id);
        // this.props.like({id:id,index:this.props.index})
    }
    onUnLikeCLick(id) {
        this.props.removeLike(id);
    }
    findUserLike(likes) {
        const { auth } = this.props;
        if (likes.filter(like => like.user === auth.user.id).length > 0) {
            return true;
        } else {
            return false;
        }
    }
    render() {
        const { post, auth, showActions } = this.props;

        //使用momentjs转换时间戳
        let date = post.date;
        var timestamp = new Date(date);
        var Time = moment(timestamp).format('YYYY-MM-DD HH:mm:ss');

        return (
            <div className="card card-body mb-3">
                <div className="row">
                    <div className="col-md-2">
                        <a href="profile.html">
                            <img className="round-circle d-nonse d-md-block"
                                src={post.avatar} alt="" />
                        </a>
                        <br />
                        <p className="text-center">{post.name}</p>
                    </div>
                    <div className="col-md-10">
                        <p className="lead">{post.text}</p>
                        {
                            showActions ? (
                                <span>
                                    <button
                                        onClick={() => this.onLikeCLick(post._id)}
                                        type="button" className="btn btn-light mr-1">
                                        <i className=
                                            {classnames("fa fa-thumbs-up",
                                                {
                                                    'text-info': this.findUserLike(post.likes)
                                                })}></i>
                                        <span className="badge badge-light">{post.likes.length}</span>
                                    </button>
                                    <button
                                        onClick={() => this.onUnLikeCLick(post._id)}
                                        type="button" className="btn btn-light mr-1">
                                        <i className="text-secondary fa fa-thumbs-down"></i>
                                    </button>
                                    <Link to={`/post/${post._id}`} className="btn btn-info mr-1">鼓励留言</Link>
                                    {
                                        post.user === auth.user.id ?
                                            (
                                                <button
                                                    onClick={() => this.onDeleteCLick(post._id)}
                                                    type="button"
                                                    className="btn btn-danger mr-1">
                                                    删除</button>
                                            ) : null
                                    }
                                </span>
                            ) : null
                        }
                    </div>
                </div>
                <div className="d-flex justify-content-between align-items-center mt-3">
                    <div></div>
                    <small>{Time}</small>
                </div>
            </div>
        )
    }
}
//设置默认属性值，用于控制单个留言中组件的显隐
PostItem.defaultProps = {
    showActions: true
}
PostItem.propTypes = {
    deletePost: PropTypes.func.isRequired,
    addLike: PropTypes.func.isRequired,
    removeLike: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    post: PropTypes.object.isRequired,
}
//将状态转化(映射)为属性
const mapStateToProps = (state) => ({
    auth: state.auth
})
export default connect(mapStateToProps, { deletePost, addLike, removeLike })(withRouter(PostItem));