import React, { Component } from 'react'
// import axios from 'axios';
import { connect } from 'react-redux';
import { loginUser } from '../../actions/authActions';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
// import classnames from 'classnames';
import TextFieldGroup from '../../common/TextFieldGroup';
import '../../assets/login.scss';

class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            password: '',
            errors: {
                email:'',
                password:''
            },
            clientHeight: document.documentElement.clientHeight || document.body.clientHeight
        };
        // this.onChange = this.onChange.bind(this);
        // this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState(
            { [e.target.name]: e.target.value }
        );
    }
    //增加路由守卫(该生命周期函数在组件组件挂载完成时被执行)
    componentDidMount() {

        if (this.props.auth.isAuthenticated) {
            this.props.history.push("/dash");
        }

    }
    componentWillUnmount() {
        // componentWillMount进行异步操作时且在callback中进行了setState操作时，需要在组件卸载时清除state
        this.setState = () => {
            return;
        };
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
            this.props.history.push("/dash");
        }
        //更新组件
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            })
        }
    }


    onSubmit(e) {
        e.preventDefault();
        const newUser = {
            email: this.state.email,
            password: this.state.password,
        };
        this.props.loginUser(newUser);
        // axios.post("/api/users/login", newUser)
        //     .then(res => console.log(res.data))
        //     .catch(err => this.setState({ errors: err.response.data }));
    }

    render() {
        const { errors } = this.state;
        // message.success('欢迎')
        // console.log(this.props)
        return (
            <div className="login">
                {/* <Particles
                    height={"100vh"}
                    params={{
                        number: { value: 50 },
                        ize: { value: 3 },
                        interactivity: {
                            events: {
                                onhover: { enable: true, mode: 'repulse' }
                            }
                        }
                    }} /> */}
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="display-4 text-center title">登录</h1>
                            <p className="lead text-center font-weight-normal title">使用已有的账户登录</p>
                            <form className="form-signin mb-5" onSubmit={e => this.onSubmit(e)}>
                                <TextFieldGroup
                                    type="email"
                                    placeholder="Email"
                                    name="email"
                                    value={this.state.email}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.email}
                                />
                                <TextFieldGroup
                                    type="password"
                                    placeholder="Password"
                                    name="password"
                                    value={this.state.password}
                                    onChange={e => this.onChange(e)}
                                    errors={errors.password} />
                                {/* <div className="form-group">
                                <input value={this.state.password} onChange={e=>this.onChange(e)}
                                className={classnames('form-control form-control-lg', {
                                    'is-invalid': errors.password
                                })} type="password"  placeholder="Password" name="password" />
                                {
                                        errors.password && (<div className="invalid-feedback">
                                            {errors.password}
                                        </div>)
                                    }
                              </div> */}
                                <button className="btn btn-lg btn-primary btn-block"
                                    type="submit">Sign in</button>
                                {/* <input type="submit" className="btn btn-lg btn-primary btn-block mt-4"/> */}
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
Login.propTypes = {
    loginUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    status: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    auth: state.auth,
    errors: state.errors,
    status: state.errors
})

export default connect(mapStateToProps, { loginUser })(withRouter(Login));
