import React, { Component } from 'react';
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import { deleteEproject } from "../../actions/profileActions";


class Eproject extends Component {
    onDeleteClick(id) {
        this.props.deleteEproject(id);
    }
    render() {
        const eproject = this.props.eproject.map(epr => (
            <tr key={epr._id}>
                <td>{epr.projectname}</td>
                <td>{epr.technology}</td>
                <td>
                    {epr.from} ~ {epr.to === '' ? '至今' : epr.to}
                </td>
                <td>
                    <button
                        data-toggle="modal"
                        data-target="#exampleModal"
                        // onClick={this.onDeleteClick.bind(this,epr._id)}
                        className="btn btn-primary">删除</button>
                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel">提示</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    是否删除项目 {epr.projectname}
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">取消</button>
                                    <button type="button"
                                        onClick={this.onDeleteClick.bind(this,epr._id)}
                                        className="btn btn-primary" data-dismiss="modal">确定</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </td>
            </tr>
        ))
        return (
            <div>
                <h4 className="mb-4">项目经历</h4>
                <table className="table">
                    <thead>
                        <tr>
                            <th>项目</th>
                            <th>技术栈</th>
                            <th>周期</th>
                            <th />
                        </tr>
                        {eproject}
                    </thead>
                </table>
            </div>
        )
    }
}
Eproject.propTypes = {
    deleteEproject: PropTypes.func.isRequired
}
export default connect(null, { deleteEproject })(Eproject);