/**
 * 私有路由，导航守卫教程参考:
 * https://ui.dev/react-router-v4-protected-routes-authentication/
 */
import React, { Component } from 'react';
// import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

import './App.css';
import Navbar from './components/layout/Navbar';
import Landing from './components/layout/Landing';
import Footer from './components/layout/Footer';

// import Login from './components/auth/Login';
// import Register from './components/auth/Register';
import Dash from './components/dash/Dash';
import CreateProfile from './components/create-profile/CreateProfile';
import EditProfile from './components/edit-profile/EditProfile';
import AddExperience from './components/add-credentials/AddExperience';
import AddEducation from './components/add-credentials/AddEducation';
import AddEproject from './components/add-credentials/AddEproject';
// import Profiles from './components/profiles/Profiles';
import Profile from './components/profile/Profile';
import Posts from './components/posts/Posts';
import Post from './components/post/Post';
import ErrorMatch from './components/ErrorMatch/ErrorMatch';

import { Provider } from 'react-redux';
import store from './store';
import setAuthToken from './utils/setAuthToken';
import { setCurrentUser, quitUser } from './actions/authActions';
import jwt_decode from 'jwt-decode';

// Provider在根组件外面包了一层，这样一来，App的所有子组件就默认都可以拿到state了。
import PrivateRoute from './common/PrivateRoute';
import routes from './routes';
//判断token是否存在，页面是否处于登录状态
if (localStorage.jwtToken) {
  setAuthToken(localStorage.jwtToken);
  //解析token
  const decoded = jwt_decode(localStorage.jwtToken);
  store.dispatch(setCurrentUser(decoded));

  //检测token是否过期
  //获取当前时间
  const currentTime = Date.now() / 1000;
  //判断当前时间是否大于token过期时间
  if (decoded.exp < currentTime) {
    //过期
    store.dispatch(quitUser());
    //TODO:清除用户信息
    //页面跳转
    window.location.href = "/";
  }
}

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Navbar />
            <Switch>
              <Route exact path="/" component={Landing} /> 
              {/* <div className="container">
              <Route exact path="/register" component={Register} />
              <Route exact path="/login" component={Login} />
              <Route exact path="/profiles" component={Profiles} /> */}
              {
                routes.map((route, index) => (
                  <Route
                    key={index}
                    path={route.path}
                    exact={route.exact}
                    render={(props) => {
                      document.title = route.meta.title
                      return (
                        <route.component props={props}></route.component>
                      )
                    }}
                  />
                ))
              }
              <Route exact path="/profile/:handle" component={Profile} />
              {/* <Route exact path="/posts/:id" component={Post} /> */}

              <PrivateRoute exact path="/dash" component={Dash} />
              <PrivateRoute exact path="/create-profile" component={CreateProfile} />
              <PrivateRoute exact path="/edit-profile" component={EditProfile} />
              <PrivateRoute exact path="/add-experience" component={AddExperience} />
              <PrivateRoute exact path="/add-education" component={AddEducation} />
              <PrivateRoute exact path="/add-eproject" component={AddEproject} />
              <PrivateRoute exact path="/feed" component={Posts} />
              <PrivateRoute exact path="/post/:id" component={Post} />
              {/* </div> */}
              {/* 网页不存在路由跳转 404*/}
              <Route component={ErrorMatch} />
            </Switch>
            <Footer />
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
