import React, { Component } from 'react'
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import { deleteEducation } from '../../actions/profileActions';
class Education extends Component {
    onDeleteClick(id) {
        this.props.deleteEducation(id);
    }
    render() {
        const education = this.props.education.map(edu => (
            <tr key={edu._id}>
                <td>{edu.school}</td>
                <td>{edu.degree}</td>
                <td>
                    {edu.from} ~ {edu.to === '' ? '至今' : edu.to}
                </td>
                <td>
                    <button
                        data-toggle="modal"
                        data-target="#exampleModalCenteredu"
                        // onClick={this.onDeleteClick.bind(this, edu._id)}
                        className="btn btn-info">
                        删除</button>
                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="exampleModalCenteredu" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalCenterTitle">提示</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                  是否删除 {edu.school} 在校经历</div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">取消</button>
                                    <button type="button" 
                                    data-dismiss="modal"
                                    onClick={this.onDeleteClick.bind(this, edu._id)}
                                    className="btn btn-primary">确定</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </td>
            </tr>
        ))
        return (
            <div>
                <h4 className="mb-4">教育经历</h4>
                <table className="table">
                    <thead>
                        <tr>
                            <th>学校</th>
                            <th>学历</th>
                            <th>年份</th>
                            <th />
                        </tr>
                        {education}
                    </thead>
                </table>
            </div>
        )
    }
}
Education.propTypes = {
    deleteEducation: PropTypes.func.isRequired
}
export default connect(null, { deleteEducation })(Education);