import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';
import TextFieldGroup from '../../common/TextFieldGroup';
import TextAreaFieldGroup from '../../common/TextAreaFieldGroup';
import { PropTypes } from 'prop-types';
import { connect } from 'react-redux';
import { addEproject } from '../../actions/profileActions';
import '../../assets/golab.scss'
class AddEproject extends Component {
    constructor(props) {
        super(props);
        this.state = {
            projectname: '',
            technology: '',
            tools: '',
            from: '',
            to: '',
            description: '',
            errors: {
                projectname: '',
                technology: '',
                tools: '',
                from: '',
                to: '',
                description: '',
            },
            // current: false,
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            })
        }
    }
    onExpChange(e) {
        this.setState(
            { [e.target.name]: e.target.value }
        )
    }
    onSubmit(e) {
        e.preventDefault();
        const eprData = {
            projectname: this.state.projectname,
            technology: this.state.technology,
            tools: this.state.tools,
            from: this.state.from,
            to: this.state.to,
            description: this.state.description,
        }
        console.log(eprData)
        this.props.addEproject(eprData, this.props.history)
    }
    render() {
        const { errors } = this.state;
        return (
            <div className="add-Eproject mgt">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <Link to="/dash" className="btn btn-light">
                                返回
                            </Link>
                            <h1 className="display-4 text-center">添加项目经历</h1>
                            <p className="lead text-center">
                                你所参与开发的项目或者个人项目
                            </p>
                            <small className="d-block pb-3">* 为必填项</small>
                            {/* 表单 */}
                            <form onSubmit={e => this.onSubmit(e)}>
                                <TextFieldGroup
                                    placeholder="* 项目名称"
                                    name="projectname"
                                    value={this.state.projectname}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.projectname}
                                />
                                <TextFieldGroup
                                    placeholder="* 技术栈"
                                    name="technology"
                                    value={this.state.technology}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.technology}
                                    info="请使用加号隔开你项目所含技术栈 (例如: React+React-redux+koa2)"
                                />
                                <TextFieldGroup
                                    placeholder="开发工具"
                                    name="tools"
                                    value={this.state.tools}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.tools}
                                    info="若所用工具较多，请使用加号隔开"
                                />
                                <h6>* 项目开始时间</h6>
                                <TextFieldGroup
                                    name="from"
                                    type="date"
                                    value={this.state.from}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.from}
                                />
                                <h6>结束时间</h6>
                                <TextFieldGroup
                                    name="to"
                                    type="date"
                                    value={this.state.to}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.to}
                                />
                                <TextAreaFieldGroup
                                    placeholder="*项目描述"
                                    name="description"
                                    value={this.state.description}
                                    onChange={e => this.onExpChange(e)}
                                    errors={errors.description}
                                    info="说明有关项目开发的相关内容等"
                                />
                                <input
                                    type="submit"
                                    value="提交"
                                    className="btn btn-info btn-block mt-4"
                                />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
AddEproject.propTypes = {
    addEproject: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};
const mapStateToProps = state => ({
    profile: state.profile,
    errors: state.errors
});
export default connect(mapStateToProps, { addEproject })(withRouter(AddEproject));