import React, { Component } from 'react'
import { Link } from 'react-router-dom';
/** react-redux 的两个最主要功能
 *  connect: 用于从 UI 组件生成容器组件, 将两种组件连起来
 *  Provider: 可以让组件及子组件拿到state
 */
import { connect } from 'react-redux';
/**
 * 属性类型检查
 */
import PropTypes from 'prop-types';
import Particles from 'react-particles-js';

class Landing extends Component {
    state = { clientHeight: document.documentElement.clientHeight || document.body.clientHeight };

    //增加路由守卫
    componentDidMount() {
        document.title = 'Awang';
        if (this.props.auth.isAuthenticated) {
            this.props.history.push("/dash");
        }
    }
    render() {
        return (
            <div className="landing ">
                <Particles
                    height={this.state.clientHeight - 5 + 'px'}
                    params={{
                        number: { value: 50 },
                        ize: { value: 3 },
                        interactivity: {
                            events: {
                                onhover: { enable: true, mode: 'repulse' }
                            }
                        }
                    }} />
                <div className="dark-overlay landing-inner text-light landingheight">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12 text-center">
                                <h1 className="display-3 mb-4">dat-lzt在线</h1>
                                <p className="lead">details make a difference</p>
                                <hr />
                                <Link to="/register" className="btn btn-lg btn-info mr-2">注册</Link>
                                <Link to="/login" className="btn btn-lg btn-light">登录</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Landing.propTypes = {
    auth: PropTypes.object.isRequired,
}
//将状态转化(映射)为属性
const mapStateToProps = (state) => ({
    auth: state.auth,
})
export default connect(mapStateToProps)(Landing);