import {
    ADD_POST,
    GET_POST,
    GET_POSTS,
    DELETE_POST,
    POST_LOADING,
    LIKE
} from '../actions/types';
const initialState = {
    posts: [],
    post: {},

    //动画加载
    loading: false
}

export default function(state = initialState, action) {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [action.payload, ...state.posts]
            }
        case GET_POSTS:
            return {
                ...state,
                posts: action.payload,
                loading: false
            }
        case GET_POST:
            return {
                ...state,
                post: action.payload,
                loading: false
            }
        case POST_LOADING:
            return {
                ...state,
                loading: true
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(
                    post => post._id !== action.payload)
            }
        case LIKE:
            return {
                ...state,
                posts: action.payload,
            }

        default:
            return state;

    }

}